
import { store } from "@/store";
import { useRoute, useRouter } from "vue-router";
import { ref, nextTick, onMounted, onUnmounted } from "vue";

import subpage from "@/components/subpage.vue";
import imageDisplayContainer from "@/components/image-component.vue";
import spotlightComponent from "@/components/spotlight-component.vue";

import { images as portfolioImages, imageDetails } from "@/store/portfolio";

export default {
  components: { subpage, imageDisplayContainer, spotlightComponent },
  setup() {
    const route = useRoute();
    const router = useRouter();

    // window.addEventListener('load', onLoad);
    // onUnmounted(() => {
    //   window.removeEventListener('load', onLoad);
    // });
    // let a: EventListenerOrEventListenerObject;
    // function onLoad(evt: Event): void {
    //   stillLoading.value = false;
    //   console.log('done loading');
    //   console.log(store.state.currentBrowser === 'firefox' && stillLoading.value);
    // }

    type galleryType = "all" | "advertisement" | "faces" | "dance" | "nudes" | "fashion";
    type galleryContent = { preview: string; full: string; ratio: number };
    type galleryList = {
      loadingState: "notLoaded" | "previewLoaded";
      visible: boolean;
      type: galleryType;
      image: galleryContent;
    };
    // type galleryListWithVis = { visible: boolean, type: galleryType; image: galleryContent };
    let gallery: {
      advertisement: galleryList[];
      faces: galleryList[];
      dance: galleryList[];
      nudes: galleryList[];
      fashion: galleryList[];
      all: galleryList[];
    } = {
      advertisement: [],
      faces: [],
      dance: [],
      nudes: [],
      fashion: [],
      all: [],
    };

    function parseImages(
      galleryType: galleryType,
      imageDetailsList: imageDetails[]
    ): galleryList[] {
      const result: galleryList[] = [];
      //fill gallery
      for (const galleryImage of imageDetailsList) {
        let resultEntry: galleryList = {
          loadingState: "notLoaded",
          visible: true,
          type: galleryType,
          image: {
            full: require(`../../public/images/portfolio/${galleryImage.fullsizePath}`),
            preview: require(`../../public/images/portfolio/${galleryImage.previewPath}`),
            ratio: galleryImage.width / galleryImage.height,
          },
        };
        result.push(resultEntry);
      }
      return result;
    }

    gallery.advertisement = parseImages("advertisement", portfolioImages.advertisement);
    gallery.dance = parseImages("dance", portfolioImages.dance);
    gallery.faces = parseImages("faces", portfolioImages.faces);
    gallery.fashion = parseImages("fashion", portfolioImages.fashion);
    gallery.nudes = parseImages("nudes", portfolioImages.nudes);

    shuffleArray(gallery.advertisement);
    shuffleArray(gallery.dance);
    shuffleArray(gallery.faces);
    shuffleArray(gallery.fashion);
    shuffleArray(gallery.nudes);

    const currentGalleryName = ref<galleryType>("all");
    const currentGalleryImages = ref<galleryList[]>([]);

    gallery.all = [
      ...gallery.advertisement,
      ...gallery.dance,
      ...gallery.faces,
      ...gallery.fashion,
      ...gallery.nudes,
    ];

    shuffleArray(gallery.all);
    currentGalleryImages.value = gallery.all;

    const stillLoading = ref(true);

    for (let index = 0; index < currentGalleryImages.value.length; index++) {
      const element = currentGalleryImages.value[index];
      // }
      // for (const iterator of currentGalleryImages.value) {
      // console.log('loaded');
      const img = new Image();
      img.src = element.image.preview;
      img.onload = (e: Event) => {
        currentGalleryImages.value[index].loadingState = "previewLoaded";

        if (
          !currentGalleryImages.value.find((obj) => {
            return obj.loadingState === "notLoaded";
          })
        ) {
          stillLoading.value = false;
          // console.log('.l');
        }
      };
    }

    function shuffleArray<T>(array: T[]) {
      return array.sort((a, b) => 0.5 - Math.random());
      // for (let i = array.length - 1; i > 0; i--) {
      //   const j = Math.floor(Math.random() * (i + 1));
      //   const temp = array[i];
      //   array[i] = array[j];
      //   array[j] = temp;
      // }
    }

    async function setGallery(value: galleryType) {
      if (value === currentGalleryName.value) {
        return;
      }

      currentGalleryName.value = value;
      router.push({ name: route.name!, query: { ...route.query, view: value } });
      // if (value !== 'all') {
      //   // console.log('all');
      if (store.state.currentBrowser !== "firefox") {
        await removeWhereNot(value, currentGalleryImages.value);
      }
      // }
      currentGalleryImages.value = [];
      if (value == "all") {
        // addFromGallery('advertisement', currentGalleryImages.value);
        // addFromGallery('faces', currentGalleryImages.value);
        // addFromGallery('dance', currentGalleryImages.value);
        // addFromGallery('nudes', currentGalleryImages.value);
        // addFromGallery('fashion', currentGalleryImages.value);

        // shuffleArray(currentGalleryImages.value);
        currentGalleryImages.value = gallery.all;
      }
      if (value == "advertisement") {
        currentGalleryImages.value = gallery.advertisement;

        // addFromGallery('advertisement', currentGalleryImages.value);
      }
      if (value == "faces") {
        currentGalleryImages.value = gallery.faces;

        // addFromGallery('faces', currentGalleryImages.value);
      }
      if (value == "dance") {
        currentGalleryImages.value = gallery.dance;

        // addFromGallery('dance', currentGalleryImages.value);
      }
      if (value == "nudes") {
        currentGalleryImages.value = gallery.nudes;

        // addFromGallery('nudes', currentGalleryImages.value);
      }
      if (value == "fashion") {
        currentGalleryImages.value = gallery.fashion;

        // addFromGallery('fashion', currentGalleryImages.value);
      }

      // console.log('added');
    }

    // function addFromGallery(
    //   value: galleryType,
    //   current: { type: galleryType; image: galleryContent }[]
    // ): void {
    //   for (const iterator of gallery[value]) {
    //     // if (
    //     //   !current.some((element) => {
    //     //     if (element.image.full === iterator.full) {
    //     //       return true;
    //     //     }

    //     //     return false;
    //     //   })
    //     // ) {
    //     current.push({ type: value, image: iterator });
    //     // }
    //   }
    // }

    async function removeWhereNot(
      value: galleryType,
      current: { type: galleryType; image: galleryContent }[]
    ) {
      // const indexToRemove: number[] = [];
      // indexesToHide.value = [];

      // if (value === 'all') {
      //   indexesToHide.value = [];
      //   return;
      // }

      // console.log(current);
      // const hideElementsIds = [];
      for (let index = currentGalleryImages.value.length - 1; index >= 0; index--) {
        const element = currentGalleryImages.value[index];

        if (value === "all") {
          currentGalleryImages.value[index].visible = true;
          continue;
        }
        if (element.type !== value) {
          // console.log(`hide ${element.type}`);
          // indexToRemove.push(index);
          currentGalleryImages.value[index].visible = false;
          // hideElementsIds.pu
          // const element = document.querySelector(`#gallery-image-n${index}`);
          // (element as HTMLElement).style.opacity = '0';
        } else {
          currentGalleryImages.value[index].visible = true;
        }
      }
      // console.log(indexToRemove);
      // indexesToHide.value = indexToRemove;
      // // console.log(indexToRemove);
      // let selectorString = '';
      // for (let index = 0; index < indexToRemove.length; index++) {
      //   const element = indexToRemove[index];
      //   if (selectorString == '') {
      //     selectorString += `#gallery-image-n${element}`;
      //   } else {
      //     selectorString += `,#gallery-image-n${element}`;
      //   }
      //   // await nextTick();
      // }
      // // console.log(selectorString);

      // const elementsToHide = document.querySelectorAll(selectorString);
      // elementsToHide.forEach((element) => {
      //   (element as HTMLElement).style.opacity = '0';
      // });

      //await transition
      await new Promise((resolve) => {
        setTimeout(() => {
          // console.log('timeout done');
          resolve(null);
          return;
        }, 500);
      });
      //
      return;
      // for (let index = 0; index < indexToRemove.length; index++) {
      //   const element = indexToRemove[index];
      //   current.splice(element, 1);
      //   // await nextTick();
      // }
    }

    onMounted(async () => {
      //gallery already on route
      if (route.query.view) {
        const queryView = route.query.view;
        // console.log(route.query.view);
        if (
          queryView == "all" ||
          queryView == "advertisement" ||
          queryView == "faces" ||
          queryView == "dance" ||
          queryView == "nudes" ||
          queryView == "fashion"
        ) {
          await new Promise((resolve) => {
            setTimeout(() => {
              resolve(null);
              return;
            }, 500);
          });
          setGallery(queryView);
        }
      }
    });

    // onMounted(() => {
    //   // new ShowOnScroll({
    //   //   selector: '.gallery-image-wrapper',
    //   //   visibleClassName: 'show-on-scroll-visible',
    //   //   // applyOnChildElemetSelector: "a",
    //   // });
    // });

    return {
      store,
      gallery: {
        currentGalleryName,
        currentGalleryImages,
        stillLoading,
      },
      functions: {
        setGallery,
      },
    };
  },
};
