
import { store } from "@/store";
import { onMounted, onUnmounted, PropType, ref } from "vue";

import { useRoute, useRouter } from "vue-router";

type galleryContent = { preview: string; full: string; ratio: number };
type galleryType = "all" | "advertisement" | "faces" | "dance" | "nudes" | "fashion";
type currentGalley = { type: galleryType; image: galleryContent }[];

export default {
  props: {
    currentGalleryList: Array as PropType<Array<{ type: galleryType; image: galleryContent }>>,
  },
  components: {},
  setup(props) {
    const route = useRoute();
    const router = useRouter();

    const imageDirection = ref<"left" | "right">("right");

    function nextImage() {
      if (store.state.portfolioView.currentGalleryIndex >= props.currentGalleryList!.length - 1) {
        store.state.portfolioView.currentGalleryIndex = 0;
        // return;
      } else {
        store.state.portfolioView.currentGalleryIndex++;
      }
      imageDirection.value = "right";
      router.push({
        name: route.name!,
        query: { ...route.query, index: store.state.portfolioView.currentGalleryIndex },
      });
    }
    function previousImage() {
      if (store.state.portfolioView.currentGalleryIndex <= 0) {
        store.state.portfolioView.currentGalleryIndex = props.currentGalleryList!.length - 1;
        // return;
      } else {
        store.state.portfolioView.currentGalleryIndex--;
      }
      imageDirection.value = "left";
      router.push({
        name: route.name!,
        query: { ...route.query, index: store.state.portfolioView.currentGalleryIndex },
      });
    }
    function closeImage() {
      let queryParams = JSON.parse(JSON.stringify(route.query));
      //   console.log(queryParams);
      if ("index" in queryParams) {
        delete queryParams.index;
      }
      router.push({
        name: route.name!,
        query: queryParams,
      });
      store.state.portfolioView.currentGalleryIndex = -1;
      //   console.log(queryParams);
      // router.replace({ query: queryParams });
    }

    window.addEventListener("keydown", handleKeys);

    onUnmounted(() => {
      window.removeEventListener("keydown", handleKeys);
    });

    function handleKeys(event: KeyboardEvent) {
      //   console.log(event);

      if (event.key === "ArrowLeft") {
        previousImage();
      }
      if (event.key === "ArrowRight") {
        nextImage();
      }
      if (event.key === "Escape") {
        closeImage();
      }

      if (event.key === "ArrowUp" || event.key === "ArrowDown" || event.key === "Tab") {
        event.preventDefault();
      }
    }

    onMounted(() => {
      const routeIndexParam = route.query.index;
      if (routeIndexParam) {
        store.state.portfolioView.currentGalleryIndex = Number(routeIndexParam);
        store.state.navigationBar.isVisible = true;
        store.state.navigationBar.transparent = false;
      }
    });

    return {
      imageDirection,
      store,
      functions: {
        nextImage,
        previousImage,
        closeImage,
      },
    };
  },
};
