
import { store } from '@/store';
import subpage from '@/components/subpage.vue';
import { useRoute, useRouter } from 'vue-router';

import { workshops } from '@/store/workshops';
import { onMounted, ref } from 'vue';
import { ShowOnScroll } from '@/functions/show-on-scroll';

export default {
  components: { subpage },
  setup(props) {
    const route = useRoute();
    const router = useRouter();

    const workshopId = String(route.params.workshopId);

    let currentWorkshop = ref(
      workshops.find((obj) => {
        return obj.id === workshopId;
      })
    );

    const workshopInvalid = ref(false);
    if (!currentWorkshop) {
      console.warn('Workshop not found');
      //   router.push({ name: 'workshops' });
      workshopInvalid.value = true;
    }
    onMounted(() => {
      new ShowOnScroll({
        selector: '.show-on-scroll',
        visibleClassName: 'show-on-scroll-visible',
        applyOnChildElemetSelector: ".delay01,.delay02,.delay03",

        // applyOnChildElemetSelector: "a",
      });
    });
    return { store, currentWorkshop, workshops, workshopInvalid };
  },
};
