
import { ref, onMounted, Ref, onUpdated } from "vue";
import diashow from "@/components/diashow.vue";
import circleLoader from "@/components/circleLoader.vue";

// import { useStore } from 'vuex';
// import { key } from '@/store';
import { store } from "@/store";

import { useRoute, useRouter } from "vue-router";

import { clickOnLink } from "@/functions/link-without-redirect";

export default {
  components: {
    diashow,
    circleLoader,
  },
  setup() {
    // const store = theStore;
    const route = useRoute();
    const router = useRouter();

    const routeComponent = ref<HTMLElement | null>(null);

    //scroll prox
    const useScrollProx = ref(false);

    //get current browser
    let userAgent = navigator.userAgent;
    console.log(userAgent);
    if (userAgent.match(/chrome|chromium|crios/i)) {
      store.state.currentBrowser = "chrome";
    } else if (userAgent.match(/firefox|fxios/i)) {
      store.state.currentBrowser = "firefox";
    } else if (userAgent.match(/safari/i)) {
      store.state.currentBrowser = "safari";
    } else if (userAgent.match(/opr\//i)) {
      store.state.currentBrowser = "opera";
    } else if (userAgent.match(/edg/i)) {
      store.state.currentBrowser = "edge";
    } else {
      store.state.currentBrowser = "other";
    }
    // console.log(store.state.currentBrowser);

    let homeShowRegularLogo = ref(false);
    let timerId: null | number = null;

    function hanldeScroll() {
      const scrollElements: NodeListOf<Element> = document.querySelectorAll(".route-component")!;
      let scrollElement = scrollElements[0];
      if (scrollElements.length > 1) {
        scrollElement = scrollElements[scrollElements.length - 1];
      }
      // console.log('a');
      if (!scrollElement) {
        return;
      }

      scrollElement.addEventListener("scroll", function () {
        if (scrollElement.scrollTop == 0) {
          //set navbar transparent
          // navbarTransparent.value = true;
          if (route.name !== "portfolio") {
            store.state.navigationBar.transparent = true;
          }
          homeShowRegularLogo.value = false;
          if (timerId !== null) {
            clearTimeout(timerId);
          }
        } else {
          store.state.navigationBar.transparent = false;
          // navbarTransparent.value = false;

          if (timerId === null) {
            timerId = setTimeout(() => {
              homeShowRegularLogo.value = true;
            }, 0.5);
          }
        }
      });
    }

    const isHomePage = ref(false);
    router.afterEach((to, from) => {
      // navbarShown.value = true;
      store.state.navigationBar.isVisible = true;
      // store.state.navigationBar.transparent = true;

      store.state.currentPage = String(to.name);
      // to and from are both route objects.
      closeNavbar();

      if (from.name === to.name) {
        const scrollElements: NodeListOf<Element> = document.querySelectorAll(".route-component")!;
        let scrollElement = scrollElements[0];
        if (to.name !== "portfolio") {
          scrollElement.scroll({ top: 0, behavior: "smooth" });
        }
      }

      if (to.name == "home") {
        // console.log(from.name);
        if (from.name !== "home") {
          // console.log("gere");
          store.state.navigationBar.transparent = true;
        }

        useScrollProx.value = true;
        isHomePage.value = true;
        // if (from.name) {
        // navbarTransparent.value = true;
        // }
      } else {
        store.state.navigationBar.transparent = true;

        if (from.name !== to.name) {
        }
        useScrollProx.value = false;
        isHomePage.value = false;
      }

      if (to.name === "portfolio") {
        store.state.navigationBar.transparent = false;
        if (to.query.index) {
          store.state.portfolioView.currentGalleryIndex = Number(to.query.index);
        } else if (!to.query.index && from.query.index) {
          store.state.portfolioView.currentGalleryIndex = -1;
        }
      }
    });

    function routeEnter() {
      // console.log('enter');
      hanldeScroll();
    }

    let navbarState: Ref<"closed" | "open"> = ref("closed");

    const navbarTransparent = ref(true);
    const navbarShown = ref(true);

    const showModal = ref(false);

    const loaderTimings = {
      instantLoadingTimer: 0.1,
      minDisplatTime: 1,
      maxDisplayTime: 10,
      currentDisplayTime: 0,
      isLoaded: false,
      showLoader: ref(true),
    };
    const loadingTimer = setInterval(() => {
      loaderTimings.currentDisplayTime += 0.1;
      if (loaderTimings.isLoaded && loaderTimings.minDisplatTime <= loaderTimings.currentDisplayTime) {
        loaderTimings.showLoader.value = false;
        clearInterval(loadingTimer);
      }
    }, 100);

    //hide on scroll
    let prevScrollpos = window.pageYOffset;
    onMounted(() => {
      window.addEventListener("load", function (event) {
        loaderTimings.isLoaded = true;
      });
    });

    function closeNavbar(clickLogo = false) {
      if (clickLogo === true) {
        //check if already on home screen, then just scroll up

        return;
      }
      navbarState.value = "closed";
    }

    function toggleNavbar() {
      if (navbarState.value === "closed") {
        navbarState.value = "open";
      } else {
        navbarState.value = "closed";
      }
    }

    return {
      isHomePage,
      routeEnter,
      routeComponent,
      store,
      navbarState,
      showLoader: loaderTimings.showLoader,
      showModal,
      useScrollProx,
      navBar: {
        prevScrollpos,
        navbarShown,
        navbarTransparent,
      },
      functions: {
        toggleNavbar,
        clickOnLink,
      },
    };
  },
};
