import { createRouter, RouteRecordRaw, createWebHistory } from 'vue-router';

import HomeView from '../views/home-view.vue';
import aboutView from '../views/about-view.vue';
import printsView from '../views/prints-view.vue';
import workshopsView from '../views/workshops-view.vue';
import workshopDetailView from '../views/workshop-detail-view.vue';
import workshopTermsAndConditionsView from '../views/workshop-terms-view.vue';
import studioView from '../views/studio-view.vue';
import newsletterView from '../views/newsletter-view.vue';
import portfolioView from '../views/portfolio-view.vue';
import contactView from '../views/contact-view.vue';
import privacyView from '../views/privacy-view.vue';
import legalView from '../views/legal-view.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    // children: [
    //   {
    //     name: 'home_gallery',
    //     path: 'gallery/:gallery',
    //     component: HomeView,
    //     children: [
    //       { name: 'home_gallery_index', path: 'index/:galleryIndex', component: HomeView },
    //     ],
    //   },
    // ],
  },
  {
    path: '/about',
    name: 'about',
    component: aboutView,
  },
  {
    path: '/prints',
    name: 'prints',
    component: printsView,
  },
  {
    path: '/workshops',
    name: 'workshops',
    component: workshopsView,
  },
  {
    path: '/workshops/terms-and-conditions',
    name: 'workshopTermsAndConditions',
    component: workshopTermsAndConditionsView,
  },
  {
    path: '/workshops/:workshopId',
    name: 'workshopDetail',
    component: workshopDetailView,
  },
  {
    path: '/studio',
    name: 'studio',
    component: studioView,
  },
  {
    path: '/newsletter',
    name: 'newsletter',
    component: newsletterView,
  },
  {
    path: '/portfolio',
    name: 'portfolio',
    component: portfolioView,
  },
  {
    path: '/contact',
    name: 'contact',
    component: contactView,
  },
  {
    path: '/privacy',
    name: 'privacy',
    component: privacyView,
  },
  {
    path: '/legal',
    name: 'legal',
    component: legalView,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
