
import { store } from "@/store";
import subpage from "@/components/subpage.vue";
import { useRoute, useRouter } from "vue-router";

import { workshops, workshop } from "@/store/workshops";
import { onMounted } from "vue";
import { ShowOnScroll } from "@/functions/show-on-scroll";

export default {
  components: { subpage },
  setup() {
    const route = useRoute();
    const router = useRouter();
    function viewDetail(workshop: workshop) {
      router.push({ name: "workshopDetail", params: { workshopId: workshop.id } });
    }

    onMounted(() => {
      new ShowOnScroll({
        selector: ".show-on-scroll",
        visibleClassName: "show-on-scroll-visible",
        // applyOnChildElemetSelector: "a",
        applyOnChildElemetSelector: ".delay01,.delay02,.delay03",
      });
    });
    return { store, workshops, viewDetail };
  },
};
